<template>
  <b-card :header="$t('Ads')">
    <b-button type="button" style="background-color: #ef402b;border-color: #C33421;color: white" @click="showLoader=false,clearObjects(),$refs.addAds.show()" class="mr-1 mb-2 mt-xl-n2 pull-right">{{$t('Add_Ads')}}</b-button>

