          <a v-on:click="clearObjects(),selectObject(data.item), $refs.deleteAds.show()" v-b-tooltip.hover :title="$t('Delete')"  class="icon anchor-pointer" ><i class="cui-trash font-2x2 icons mt-1"></i></a>
