        <b-button type="button" style="background-color: #ef402b;border-color: #C33421;color: white" v-on:click="deleteItem($refs.deleteAds,deleteAds.AdvertisementID),clearObjects()" class="float-right ml-2 " variant="primary"><i class="fa fa-dot-circle-o"></i> {{$t('Confirm')}}</b-button>
      </div>
    </b-modal>

