                        width: 50%;
                        padding: 10px;"
                        
            ></vue-upload-multiple-image>

            </b-form-group>
            </b-col>
        </b-row>
        <!-- <b-row>
          <b-col md="12">
            <b-form-group style="margin-bottom: -10px"
              :label="$t('Add_Pictures')">
            </b-form-group>
           <hr style="margin-top: 0.5rem !important;" />
          </b-col>
        </b-row>
        <b-row>
          <b-col md="6">
             <b-form-group> 
              <b-form-file
                v-model="saveAds.advertisementsImages"
                @change="handleFileUpload($event)"
                :state="Boolean(advertisementsImages)"
                :placeholder="$t('Choose_advertising_Images')"
                :drop-placeholder="$t('Drop_file_here')"
                multiple  required
              />
             </b-form-group>
             
          </b-col>
        </b-row>
          <b-row>

          <b-col md="6">
             <b-form-group>
               <b-form-textarea v-model="fileName" v-bind:disabled="true"></b-form-textarea>
             </b-form-group>
          </b-col>
        </b-row>
        <b-row> 
          
          <div v-for="(image, key) in advertisementsImages" v-bind:key="image">
            <b-col> 
                  <img
                  class="preview"
                  v-bind:ref="'image' +parseInt( key )"
                  v-show="showPreview"
                  :state="Boolean(advertisementsImages)"
                  fluid
                  alt=""
