                  rounded="circle"
                  width="100"
                  height="100"
                  border="5"
                  padding="center"
                  />
            </b-col>
          </div>
        </b-row> -->
        <hr style="margin: 20px -20px;" />
        <div slot="modal-footer" class="w-100 text-right">
          <b-button type="submit" class="ml-2" v-if="showLoader==false" style="background-color: #ef402b;border-color: #C33421;color: white"><i class="fa fa-dot-circle-o"></i> {{$t('Save')}}</b-button>
           <b-button variant="primary" v-if="showLoader==true">
            <b-spinner small></b-spinner>
