               @before-remove="beforeRemove"
               @edit-image="editImage"
                @data-change="dataChange"
                :data-images="save_images"
                idUpload="myIdUploadEdit"
                style="margin: auto;
                        width: 50%;
                        padding: 10px;"
                        
            ></vue-upload-multiple-image>

            </b-form-group>
            </b-col>

           <b-col md="6">
              <b-row>
                <b-form-group style="margin-bottom: -10px"
                  :label="$t('Advertising_Products_Pictures')">
                  </b-form-group>
                    <br> <br>
              </b-row> 
               
            <b-row>  
                    <b-col md="6" v-for="image in advertisementsImages1" v-bind:key="image.AdvImageURL">
                    <b-img  thumbnail :src="ApiEndPoints+'images/'+image.AdvImageURL"  rounded="left" style="height:100px;width:100px;z-index: 10;" ></b-img>
                    
                      
            <button type="button" class="close" aria-label="Close" style="position: relative;right: 44%; top: 2%;" v-on:click="addDeleteProductImgs(image)">
            <span aria-hidden="true">&times;</span>
              </button>
                      <br> <br>
