              </b-row>
            </b-col>


            
        </b-row>
        <!-- <b-row>
          <b-col md="2" v-for="image in advertisementsImages1" v-bind:key="image">
          <b-img  thumbnail :src="ApiEndPoints+'images/'+image.AdvImageURL"  rounded="left" style="height: 15vh; width:15vh" ></b-img>
          </b-col>
        </b-row> -->
        <hr style="margin: 20px -20px;" />
        <div slot="modal-footer" class="w-100 text-right">
          <b-button type="submit" class="ml-2" v-if="showLoader==false" style="background-color: #ef402b;border-color: #C33421;color: white"><i class="fa fa-dot-circle-o"></i> {{$t('Save')}}</b-button>
           <b-button variant="primary" v-if="showLoader==true">
            <b-spinner small></b-spinner>
            <span class="sr-only">Loading...</span>
          </b-button> 
          <b-button type="button" class="ml-2" v-on:click="$refs.editAdsModel.hide(),clearObjects()" ><i class="fa fa-close"></i> {{$t('Cancel')}}</b-button>
        </div>
      </b-form>
    </b-modal>
  </b-card>
</template>

<script>
import VueNotifications from 'vue-notifications'
import * as toutPaieService from "./ToutPaieService.js";
import { setTimeout } from 'timers';
import VueUploadMultipleImage from 'vue-upload-multiple-image';

export default {
  props: {
    caption: {
      type: String,
      default: 'Managers'
    },
    hover: {
      type: Boolean,
      default: true
    },
    striped: {
      type: Boolean,
      default: true
    },
    bordered: {
      type: Boolean,
      default: false
    },
    small: {
      type: Boolean,
      default: false
    },
    fixed: {
      type: Boolean,
      default: false
    },
    dark: {
      type: Boolean,
      default: false
    }
  },

  components : {

    VueUploadMultipleImage
	},
  data: () => {
    return {
      saveAds:{
        advertisementsImages:[]
      },
      editAds:{},
      deleteAds:{},
      ApiEndPoints:"",
      editMode:false,
      showPreview:false,
      save_images:[],
      loginUser:{},
      productsImages1:[],
      showLoader:false,
      removeProductImgs:[],
      advertisementsImages:[],
      advertisementsImages1:[],
      selladsList:[],
      buyadsList:[],
      adsList:[],
      sortBy : 'firstName',
      pageOptions: [5, 10, 15],
      currentPage: 1,
      perPage:10,
      file: null,
      fileName: ''
    }
  },
  computed: {
    fields () {
      return [
        {key: 'ArticleName',label: this.translateString('Article_Name'),  sortable: true},
        {key: 'ArticleDescription',label: this.translateString('Article_Description'),  sortable: true},
        {key: 'ItemPrice', label: this.translateString('Price'), sortable: true},
        {key: 'StreetName', label: this.translateString('Address'), sortable: true},
        {key: 'actions' , label: this.translateString('Action'), sortable:false, thStyle: {'width': '80px !important', 'min-width': '80px !important'}}
     ]
    },
    gridData: function() {
      return this.adsList;

    },
    totalRows: function () {
      return this.getRowCount()
    },
    captions: function() {
      return this.fields
    }
  },
  methods: {
    translateString : function(colName) {
      return this.$i18n.t(colName);
    },
    getRowCount: function () {
      return this.adsList.length ; // this.managersList ? this.managersList.length : 0;
    },
    selectObject: function(Ads){
      // console.log(Ads);
       this.editAds=Ads;
      //  if(this.editAds.advertisementsImages.length>0){
      //  this.editAds.PhotoUR_view=this.editAds.advertisementsImages[0].AdvImageURL;
      //  this.editAds.PhotoUR_Preview=toutPaieService.ApiEndPoints+"images/"+this.editAds.PhotoUR_view;
      //  }
      //  this.advertisementsImages=this.editAds.advertisementsImages;
       this.advertisementsImages1=this.editAds.advertisementsImages;
      //  this.editAds.imgNames=""
      //  for(var i=0;i<this.advertisementsImages1.length;i++){
      //    if(i==0){
      //       this.editAds.imgNames=this.advertisementsImages1[i].AdvImageURL;
      //    }else
      //     if(this.advertisementsImages1[i].AdvImageURL!=null &&this.advertisementsImages1.length>1) {
      //    //this.advertisementsImages1[i].AdvImageURL=this.advertisementsImages1[i].AdvImageURL;
      //    this.editAds.imgNames=this.editAds.imgNames+","+this.advertisementsImages1[i].AdvImageURL;
      //    }else if(this.advertisementsImages1[i].AdvImageURL!=null&&this.advertisementsImages1.length==1 ){
      //      this.editAds.imgNames=this.advertisementsImages1[i].AdvImageURL;
      //    }

      //  }
        //console.log(this.advertisementsImages);
       this.deleteAds=Ads;
    },


    dataURItoBlob(dataURI) {
    var byteString = atob(dataURI.split(',')[1]);
    var ab = new ArrayBuffer(byteString.length);
    var ia = new Uint8Array(ab);
    for (var i = 0; i < byteString.length; i++) {
        ia[i] = byteString.charCodeAt(i);
    }
    return new Blob([ab], { type: 'image/jpeg' });
},

uploadImageSuccess(formData, index, fileList) {
      let arr = [];
      fileList.forEach(element => {
        arr.push(this.dataURItoBlob(element.path));
      });
      //  console.log('arr',arr);
      this.advertisementsImages = arr;
      this.save_images = fileList;
     
     

    },
    beforeRemove (index, done, fileList) {
      //console.log('index', index, fileList);
  
      for (let i = 0; i < this.advertisementsImages.length; i++) {
        if (i === index) {
          this.advertisementsImages.splice(i, 1);
        }
      }
      console.log('index', index, fileList);
      // console.log('this.saveAds.advertisementsImages',this.advertisementsImages);
  
    
      
      //var r = confirm("remove image")
      //if (r == true) {
        done()
     // } else {
      //}
    },
    editImage (formData, index, fileList) {
      console.log('edit data', formData, index, fileList)
    },
    dataChange (data) {
      console.log("data ",data  )
    },


    deleteItem: function (ref,id) {

      console.log('in delete function');

      let URL = 'advertisements/delete/' + id;
     
      toutPaieService.getData(URL).then(res => {
       let msg=res.status;
       if (msg=="Success") {
          
          ref.hide();
          VueNotifications.success({message:"ProductGroup Deleted Successfully  ",timeout:2500});
          this.getAds();
          this.clearObjects();
          
        }else{
           VueNotifications.error({message:msg,timeout:2500});
        }

    });

    },
    updateItem: function(ref,editAds){
      console.log('in update function');
      this.editMode=true;
      this.saveItem(ref,editAds);

    },
    clearObjects: function(){
      console.log('in update function');
      this.editAds={};
      this.saveAds={};
      this.removeProductImgs=[];
      this.advertisementsImages=[];
      this.save_images=[];
      this.showLoader=false;
        this.getAds();
	if (document.getElementById('myIdUploadSave') != null && document.getElementById('myIdUploadSave') != undefined) {
      document.getElementById('myIdUploadSave').value = '';
}
     if (document.getElementById('myIdUploadEdit') !=null && document.getElementById('myIdUploadEdit') != undefined) {
      document.getElementById('myIdUploadEdit').value = '';
}
    },

     addDeleteProductImgs: function(img) {
       console.log(img);
       console.log(this.advertisementsImages1);
       this.removeProductImgs.push(img);
       var index=null;
          
           for (let i = 0; i < this.advertisementsImages1.length; i++) {
                  if(this.advertisementsImages1[i].AdvertisementsPhotoID==img.AdvertisementsPhotoID){
                        index= i;  
                  }
            
            }

        if(index!=null){
             for (let i = 0; i < this.advertisementsImages1.length; i++) {
                if (i === index) {
                  this.advertisementsImages1.splice(i, 1);
                }
           }
      }
      

     },
     

     deleteProductImg: function(img) {

     console.log("delete image",img);

     let url="deleteAdvertisementsImage/"+img.AdvertisementsPhotoID;
     
    //  toutPaieService.saveData(img, url).then(data => {
    //    // console.log('SAVEdata', form);
         
    //    let msg=data.data[0].message;
    //     if (data.status == 200 && msg=="Success") { 
    //        this.getAds();
    //      VueNotifications.success({message:"Product Image Deleted Successfully Saved",timeout:2500});
    //       //window.location.reload();
    //     }else{
    //        VueNotifications.error({message:msg,timeout:2500});
    //     }   
         
    //   });


    toutPaieService.getData(url).then(res => {
      
        //console.log("this.selladsList",this.selladsList);
         console.log("res :",res);
      });



    },

    handleFileUpload: function(e) {
 
      var selectedFiles = e.target.files;
      this.imgsname = e.target.files[0].name;
      
      

      if (/\.(jpe?g|png|gif)$/i.test(this.imgsname)) {
      for (let i = 0; i < selectedFiles.length; i++) {

        if (selectedFiles[i].size > 1024*1024) {
        e.preventDefault();
    
        alert(selectedFiles[i].name+' file should not greater than 1MB');
        this.advertisementsImages=[];
        selectedFiles=[];
        this.saveAds.advertisementsImages=[];
        break;
      }else{
        this.advertisementsImages.push(selectedFiles[i]);
      }
      

      for (let i = 0; i < this.advertisementsImages.length; i++) {
      let reader = new FileReader(); //instantiate a new file reader
      reader.addEventListener(
      "load",
      function() {
      this.$refs["image" + parseInt(i)][0].src = reader.result;
      this.showPreview = true;
      }.bind(this),
      false
      ); //add event listener

      reader.readAsDataURL(this.advertisementsImages[i]);
      }
      }
      } 
       
},



    getFilesName: function () {
      setTimeout(() => {
        // console.log('this.file', this.file);
        this.file.forEach(file => {
          this.fileName = this.fileName + file.name;
          this.fileName = this.fileName + ', ';
        });
      }, 500)
    },
    getAds: function () {
      let urlBuy="/advertisements/get/buy";
      let urlSell="/advertisements/get/sell";
       this.adsList=[];
       this.selladsList =[];
       this.buyadsList=[];

       toutPaieService.getData(urlBuy).then(res => {
        this.buyadsList = res.data;


        toutPaieService.getData(urlSell).then(res => {
        this.selladsList = res.data;
        this.adsList=this.buyadsList.concat(this.selladsList);
      //   for(var i=0;i<this.selladsList.length;i++){
      //   this.adsList.push(this.selladsList[i]);
      // }
        console.log("this.selladsList",this.selladsList);
         console.log("this.adsList::",this.adsList);
      });

      //  for(var i=0;i<this.buyadsList.length;i++){
      //   this.adsList.push(this.buyadsList[i]);
      // }
        // console.log("this.buyadsList",this.buyadsList);
        //  console.log("this.adsList::",this.adsList);
      });

    },
    
    saveItem: function(ref,saveAds){
      console.log('in save itom function');
          this.loginUser=JSON.parse(window.localStorage.getItem("loginUser"));
          
      let userData = saveAds;

      //userData.advertisementsImages=this.advertisementsImages;

      if(this.editMode){
            if(this.removeProductImgs.length>0){
                 for(var d=0;d<this.removeProductImgs.length;d++){

                   this.deleteProductImg(this.removeProductImgs[d]);        
                 }
            }

            
          }




     // console.log(this.advertisementsImages);
      //userData.advertisementsImages=this.advertisementsImages;
      // console.log("userData",userData);     

      var form = new FormData();
      form.append("ArticleName", userData.ArticleName);
      form.append("ArticleDescription",userData.ArticleDescription);
      form.append("ItemPrice",userData.ItemPrice);
      form.append("StreetName", userData.StreetName);
      form.append("PostalCode",userData.PostalCode);
       form.append("City",userData.City);
       form.append("UserID",this.loginUser.UserID);
      form.append("AdsType",userData.AdsType);


      if(this.editMode === true){
        userData.advertisementsImages=[];
      }
        
        if(this.advertisementsImages.length>0){

          for(var i=0;i<this.advertisementsImages.length;i++){
      form.append("AdvImageURL["+i+"]",this.advertisementsImages[i]);
       }

        }else{
             form.append("AdvImageURL[0]","");
       }
      
       this.advertisementsImages=[];
       
      let url =  null;
      if (this.editMode === true && userData.AdvertisementID) {
        url = 'advertisements/update/' + userData.AdvertisementID;
        this.editMode = false;
       
      }else{
        url = 'advertisements/save';
      }
      //  console.log("url",url);

      toutPaieService.saveData(form, url).then(data => {
        // console.log('data', data);
        let msg=data.data[0].message;
 
        if (data.status == 200 && msg=="Success") { 
          ref.hide();
          this.advertisementsImages=[];
          this.showLoader=false;
          this.adsList=[];
          this.getAds();
          this.clearObjects();
          VueNotifications.success({message:"Ads Successfully Saved",timeout:2500});
        }else{
          this.showLoader=false;
           VueNotifications.error({message:msg,timeout:2500});
        }
      });
    },





  },
  beforeMount(){
   this.ApiEndPoints=toutPaieService.ApiEndPoints;
    this.getAds();
  
  }
}
</script>
